import Vue from 'vue';
import Router from 'vue-router';
import store from '../store/store';
import * as types from '../store/types';
import {routerMode} from '@/config/env';

import login from '@/page/login'
import layout from '@/page/layout';
import datasource from '@/page/datasource/DataSource.vue';

//
import chart_list from '@/page/chart/ChartList'
import make_chart from '@/page/chart/MakeChart'

//
import screen_list from '@/page/screen/ScreenList'
import make_screen from '@/page/screen/MakeScreen'

Vue.use(Router)
const routes = [
  {path: '/login' , name: '', component: login},
  {path: '/make_screen' , name: '', props:true, component: make_screen},
  {
    path: '/' , name: '', component: layout, redirect:'/datasource',  meta: {requireAuth: true},
    children: [
      {path:'/datasource', name:'', component: datasource},

      {path:'/chart_list', name:'', props:true,component: chart_list},
      {path:'/make_chart', name:'', props:true, component: make_chart},

      {path:'/screen_list', name:'', props:true,component: screen_list},
    ]
  }
]

const router = new Router({mode: routerMode, routes: routes});

router.beforeEach((to, from, next) => {
  let userinfo = store.getters[types.USER] || null
  if (to.matched.some(r => r.meta.requireAuth)) {
    if (userinfo) {
      if (to.path === "/index") {
        next()
      } else {
        next();
      }

    }
    else {
      next({
        path: '/login',
        query: {redirect: to.fullPath}
      })
    }
  }
  else {
    next();
  }

});

export default router;



